<template>
    <div>
        <Head headback='false' headto='false'  bol='搜索地址'></Head>
        <div class="main">
            <div class="addsearch">
                <div class="conlist">
                     <input type="text" placeholder="请输入小区/写字楼/学校等" v-model="adds">
                 </div>
                 <div class="btns" @click="btns()">确认</div>
            </div>
            <div class="title">为了满足商家的送餐要求，建议您从列表中选择地址</div>
            <div class="addwarn" v-if="show">
                <p>找不到地址？</p>
                <p>请尝试输入小区、写字楼或学校名</p>
                <p>详细地址 (如门牌号) 可稍后输入哦。</p>
            </div>
            <ul class="addlist" v-if="!show">
                <router-link tag='li' v-for="(item,index) of addlist" :key="index" :to="{path:'/Message',query:{name:item.name,geo:item.geohash}}" >
                    <p class="p1">{{item.name}}</p>
                    <p class="p2">{{item.address}}</p>
                </router-link>
            </ul>
        </div>
    </div>
</template>

<script>
import Head from './../header/Head'
export default {
components: {
    Head
},
data () {
    return {
        adds:'',
        addlist:[],
        show:true
    }
},
methods: {
    btns(){
        if(this.adds!=""){
            var that = this
            this.http.get('http://elm.cangdu.org/v1/pois?type=nearby&keyword='+this.adds).then(res=>{
                console.log(res)
                that.addlist=res
                if(that.addlist.length==0){
                    this.show=true
                }else {
                    this.show=false
                }
            })
        }
    }
}
}
</script>

<style lang='scss' scoped>
.main {
    width: 100%;
    height: 100%;
    padding-top: 0.88rem;
    .addsearch{
        width: 7.04rem;
        height: 0.77rem;
        background: white;
        padding: 0.23rem;
        display: flex;
        .conlist {
            width: 5.52rem;
            height: 0.74rem;
            border: 0.01rem solid #ddd;
            background: #f2f2f2;
            border-radius: 0.04rem;
            margin-bottom: 0.18rem;
            position: relative;
            input {
                width: 5.13rem;
                height: 0.36rem;
                border: none;
                background: #f2f2f2;
                position: absolute;
                left: 0.14rem;top:0.19rem;
                font-size: 0.3rem;
            }
        }
        .btns {
            width: 1.4rem;
            height: 0.76rem;
            border-radius: 0.06rem;
            background: #3199e8;
            color: white;
            font-size: 0.3rem;
            line-height: 0.76rem;
            margin-left: 0.1rem;
        }
    }
    .title{
        width: 100%;
        height: 0.38rem;
        background: #fff6e4;
        color: #ff883f;
        padding: 0.1rem 0rem;
        font-size: 0.29rem;
    }
    .addwarn {
        width: 100%;
        height: auto;
        margin-top: 2rem;
        font-size: 0.26rem;
        color: #969696;
        p {
            margin-bottom: 0.18rem;
        }
    }
    .addlist{
        width: 100%;
        height: auto;
        li {
            width: 7.12rem;
            height: 0.9rem;
            padding: 0.1rem 0.19rem;
            font-size: 0.3rem;
            color: #969696;
            text-align: left;
                border-bottom: 1px solid #ccc;
            .p1 {
                width: 100%;
                height: 0.4rem;
                margin-bottom: 0.1rem;
            }
            .p2 {
                width: 100%;
                height: 0.4rem;
            }
        }
    }
}
</style>



